body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #f8f9fa;
}

.contenedor {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 20px;
}

.titulo {
  font-size: 1.8rem;
  color: #007bff;
  margin-bottom: 20px;
  text-align: center;
}

.lector {
  width: 100%;
  max-width: 350px;
  aspect-ratio: 1 / 1;
  background-color: #fff;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 0 20px rgba(0,0,0,0.1);
}

.resultado {
  margin-top: 20px;
  font-size: 1rem;
  color: #333;
  word-break: break-word;
  text-align: center;
}
